<template>
  <div class="div">
    <template v-if="sortSelectValue !== 'ALL'">
      <template v-for="(item, prop) in list">
        <template v-if="item.type === sortSelectValue">
          <div class="list-item" :key="prop">
            <span class="budget-comment">{{ item.comment }}</span>
            <span class="budget-value">{{ item.value }}</span>
            <ElButton type="danger" size="mini" @click="onDeleteButton(item.id)">Delete</ElButton>
          </div>
        </template>
      </template>
    </template>
    <template v-else>
      <div class="list-item" v-for="(item, prop) in list" :key="prop">
        <span class="budget-comment">{{ item.comment }}</span>
        <span class="budget-value">{{ item.value }}</span>
        <ElButton type="danger" size="mini" @click="onDeleteButton(item.id)">Delete</ElButton>
      </div>
    </template>
    <DialogConfirm :dialogVisible="dialogVisible" @clickedDelete="deleteItem" />
  </div>
</template>


<script>
import DialogConfirm from "./DialogConfirm";

export default {
  name: "BudgetListItem",
  components: {
    DialogConfirm
  },
  data: () => ({
    dialogVisible: false,
    id: 0
  }),
  props: {
    list: {
      type: Object,
      default: () => ({})
    },
    sortSelectValue: {
      type: String,
      default: "ALL"
    }
  },
  methods: {
    onDeleteButton(id) {
      this.dialogVisible = true;
      this.id = id;
    },
    deleteItem(res) {
      if (res === true) {
        this.$emit("deleteItem", this.id);
      }
      this.dialogVisible = false;
    }
  }
};
</script>


<style scoped>
.list-item {
  display: flex;
  align-items: center;
  padding: 10px 15px;
}
.budget-value {
  font-weight: bold;
  margin-left: auto;
  margin-right: 20px;
}
</style>