<template>
  <div class="total-value" :style="selectColor(total)">Balance: {{ total }}</div>
</template>

<script>
export default {
  name: "TotalBalance",
  props: {
    total: {
      type: Number,
      default: 0
    }
  },
  methods: {
    selectColor(total) {
      if (total > 0) {
        return "color:green";
      } else if (total === 0) {
        return "color:black";
      } else {
        return "color:red";
      }
    }
  }
};
</script>

<style scoped>
.total-value {
  font-size: 26px;
  text-transform: uppercase;
  padding: 20px;
  text-align: center;
}
</style>