<template>
  <div class="dialog-confirm">
    <el-dialog :title="title" :visible.sync="dialogVisible" width="30%" :show-close=false>
      <span>Do you really want to delete this item?</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="clickedDelete">Cancel</el-button>
        <el-button type="primary" @click="clickedDelete">Confirm</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "DialogConfirm",
  data: () => ({
    title: "Delete request"
  }),
  props: {
    dialogVisible: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    clickedDelete(e) {
      const cencelBtn = e.target.parentElement.classList.contains(
        "el-button--default"
      );
      const cencelTxt = e.target.classList.contains("el-button--default");

      this.$emit("clickedDelete", !cencelBtn && !cencelTxt);
    }
  }
};
</script>

