<template>
  <div class="budget-list-wrap">
    <ElCard :header="header">
      <ElSelect class="sort-select" v-model="sortSelectValue" placeholder="Sort budget list">
        <ElOption lable="Income" value="INCOME" />
        <ElOption lable="Outcome" value="OUTCOME" />
        <ElOption lable="All" value="ALL" />
      </ElSelect>
      <template v-if="!isEmpty">
        <BudgetListItem :list="list" @deleteItem="onDeletItem" :sortSelectValue="sortSelectValue"/>
      </template>
      <ElAlert v-else type="info" :title="emptyTitle" :closable="false" />
    </ElCard>
  </div>
</template>


<script>
import BudgetListItem from "./BudgetListItem";

export default {
  name: "BudgetList",
  components: {
    BudgetListItem
  },
  props: {
    list: {
      type: Object,
      default: () => ({})
    }
  },
  data: () => ({
    header: "Budget List",
    emptyTitle: "Empty List",
    sortSelectValue: "ALL"
  }),
  computed: {
    isEmpty() {
      return !Object.keys(this.list).length;
    }
  },
  methods: {
    onDeletItem(id) {
      this.$emit("deleteItem", id);
    }
  }
};
</script>

<style scoped>
.budget-list-wrap {
  max-width: 500px;
  margin: auto;
}
</style>